import React from 'react';
import { Link, navigate } from 'gatsby';
import { UserState, useUserStore } from '../store/user';
import { Platform } from '../types/League';
import { MessageCard } from '../components/MessageCard';
import { copyUrlToClipboard } from '../utils';
import { Loading } from '../components/Loading';
import { APP_ROUTES } from '../constants';

const ThankYou = () => {
    const [values, setValues] = React.useState<{ platform: Platform | ''; leagueid: string }>({
        platform: '',
        leagueid: '',
    });
    const [loading, setLoading] = React.useState(true);
    const { user } = useUserStore((state: UserState) => {
        return {
            user: state.user,
        };
    });

    React.useEffect(() => {
        if (typeof window !== 'undefined') {
            const urlParams = new URLSearchParams(window.location.search);
            const platform = urlParams.get('platform') as Platform;
            const leagueid = urlParams.get('leagueid') as string;

            if (platform && leagueid) {
                setValues({ platform, leagueid });
                setLoading(false);
            } else {
                navigate('/');
            }
        }
    }, []);

    if (loading) return <Loading />;

    let url = `https://fantasy-franchise.com/${values.platform}/${values.leagueid}`;

    return (
        <main className="message-page-container">
            <MessageCard logoPosition="center">
                <div className="mt-6 text-center">
                    <h1 className="text-lg">Thank you for registering</h1>
                    <h2 className="text-sm font-semibold text-primary-400">
                        Please copy and save the following URL
                    </h2>

                    <div className="flex flex-col items-center gap-3 mt-6 md:flex-row md:gap-0">
                        <div className="flex items-center justify-center p-3 py-2.5 text-sm bg-gray-100 rounded-md md:rounded-r-none">
                            {url}
                        </div>
                        <button
                            className="md:!rounded-l-none base-button shrink-0 w-max"
                            onClick={() => copyUrlToClipboard(url)}
                        >
                            Copy Link
                        </button>
                    </div>

                    {user && (
                        <div className="flex justify-center mt-8 mb-4">
                            <Link to={APP_ROUTES.DASHBOARD}>Go to your dashboard</Link>
                        </div>
                    )}
                </div>
            </MessageCard>
        </main>
    );
};

export default ThankYou;
